}
.br-theme-css-stars {
	.br-widget {
		height: 28px;
		white-space: nowrap;
		a {
			text-decoration: none;
			height: 18px;
			width: 18px;
			float: left;
			font-size: 23px;
			margin-right: 5px;
			&:after {
				content: "\2605";
				color: #d2d2d2;
			}
		}
		a.br-active {
			&:after {
				color: #EDB867;
			}
		}
		a.br-selected {
			&:after {
				color: #EDB867;
			}
		}
		.br-current-rating {
			display: none;
